import { Task } from '../types/task';

class NotificationService {
  private static instance: NotificationService;
  private notificationTimers: Map<string, NodeJS.Timeout>;

  private constructor() {
    this.notificationTimers = new Map();
  }

  public static getInstance(): NotificationService {
    if (!NotificationService.instance) {
      NotificationService.instance = new NotificationService();
    }
    return NotificationService.instance;
  }

  public async requestPermission(): Promise<boolean> {
    if (!('Notification' in window)) {
      console.warn('');
      return false;
    }

    if (Notification.permission === 'granted') {
      return true;
    }

    const permission = await Notification.requestPermission();
    return permission === 'granted';
  }

  public scheduleNotification(task: Task): void {
    if (!task.notificationEnabled || !task.notificationTime) {
      return;
    }

    // 
    this.clearNotification(task.id);

    const now = new Date();
    const notificationTime = new Date(task.notificationTime);

    if (notificationTime <= now) {
      return;
    }

    const timeUntilNotification = notificationTime.getTime() - now.getTime();
    const timer = setTimeout(() => {
      this.showNotification(task);
      this.handleRepeatTask(task);
    }, timeUntilNotification);

    this.notificationTimers.set(task.id, timer);
  }

  public clearNotification(taskId: string): void {
    const existingTimer = this.notificationTimers.get(taskId);
    if (existingTimer) {
      clearTimeout(existingTimer);
      this.notificationTimers.delete(taskId);
    }
  }

  private showNotification(task: Task): void {
    if (Notification.permission !== 'granted') {
      return;
    }

    new Notification('', {
      body: task.title,
      icon: '/favicon.ico',
      tag: task.id,
    });
  }

  private handleRepeatTask(task: Task): void {
    if (task.repeatType === 'none') {
      return;
    }

    const nextDue = this.calculateNextDueDate(task);
    if (nextDue) {
      const updatedTask = {
        ...task,
        notificationTime: nextDue,
      };
      this.scheduleNotification(updatedTask);
    }
  }

  private calculateNextDueDate(task: Task): Date | null {
    if (!task.notificationTime) {
      return null;
    }

    const currentDate = new Date(task.notificationTime);
    let nextDate: Date;

    switch (task.repeatType) {
      case 'daily':
        nextDate = new Date(currentDate);
        nextDate.setDate(currentDate.getDate() + 1);
        break;
      case 'weekly':
        nextDate = new Date(currentDate);
        nextDate.setDate(currentDate.getDate() + 7);
        break;
      case 'monthly':
        nextDate = new Date(currentDate);
        nextDate.setMonth(currentDate.getMonth() + 1);
        break;
      default:
        return null;
    }

    return nextDate;
  }
}

export const notificationService = NotificationService.getInstance(); 