import { Task, TaskPriority } from '../types/task';
import { notificationService } from './notificationService';

class TaskStorage {
  private readonly STORAGE_KEY = 'tasks';

  public getTasks(): Task[] {
    const tasksJson = localStorage.getItem(this.STORAGE_KEY);
    if (!tasksJson) return [];

    try {
      const tasks = JSON.parse(tasksJson);
      return tasks.map((task: any) => ({
        ...task,
        createdAt: new Date(task.createdAt),
        reminder: task.reminder ? new Date(task.reminder) : undefined,
        notificationTime: task.notificationTime ? new Date(task.notificationTime) : undefined,
        lastCompleted: task.lastCompleted ? new Date(task.lastCompleted) : undefined,
        nextDue: task.nextDue ? new Date(task.nextDue) : undefined,
      }));
    } catch {
      return [];
    }
  }

  private saveTasks(tasks: Task[]): void {
    localStorage.setItem(this.STORAGE_KEY, JSON.stringify(tasks));
  }

  public addTask(taskData: Partial<Task>): Task {
    const tasks = this.getTasks();
    const newTask: Task = {
      id: crypto.randomUUID(),
      title: taskData.title || '',
      completed: false,
      priority: taskData.priority || 'medium',
      category: taskData.category || 'other',
      createdAt: new Date(),
      repeatType: taskData.repeatType || 'none',
      notificationEnabled: taskData.notificationEnabled || false,
      notificationTime: taskData.notificationTime,
    };

    tasks.push(newTask);
    this.saveTasks(tasks);

    if (newTask.notificationEnabled && newTask.notificationTime) {
      notificationService.scheduleNotification(newTask);
    }

    return newTask;
  }

  public updateTask(taskId: string, updates: Partial<Task>): Task | null {
    const tasks = this.getTasks();
    const taskIndex = tasks.findIndex(t => t.id === taskId);
    if (taskIndex === -1) return null;

    const updatedTask = { ...tasks[taskIndex], ...updates };
    tasks[taskIndex] = updatedTask;
    this.saveTasks(tasks);

    // 
    if (updatedTask.notificationEnabled && updatedTask.notificationTime) {
      notificationService.scheduleNotification(updatedTask);
    } else {
      notificationService.clearNotification(taskId);
    }

    return updatedTask;
  }

  public deleteTask(taskId: string): boolean {
    const tasks = this.getTasks();
    const taskIndex = tasks.findIndex(t => t.id === taskId);
    if (taskIndex === -1) return false;

    notificationService.clearNotification(taskId);
    tasks.splice(taskIndex, 1);
    this.saveTasks(tasks);
    return true;
  }

  public toggleTaskCompletion(taskId: string): Task | null {
    const tasks = this.getTasks();
    const taskIndex = tasks.findIndex(t => t.id === taskId);
    if (taskIndex === -1) return null;

    const task = tasks[taskIndex];
    const completed = !task.completed;
    const updates: Partial<Task> = { completed };

    if (completed && task.repeatType !== 'none') {
      updates.lastCompleted = new Date();
      updates.nextDue = this.calculateNextDueDate(task);
      
      if (updates.nextDue) {
        updates.notificationTime = updates.nextDue;
      }
    }

    const updatedTask = { ...task, ...updates };
    tasks[taskIndex] = updatedTask;
    this.saveTasks(tasks);

    if (updatedTask.notificationEnabled && updatedTask.notificationTime) {
      notificationService.scheduleNotification(updatedTask);
    }

    return updatedTask;
  }

  private calculateNextDueDate(task: Task): Date | null {
    if (!task.notificationTime) {
      return null;
    }

    const currentDate = new Date(task.notificationTime);
    let nextDate: Date;

    switch (task.repeatType) {
      case 'daily':
        nextDate = new Date(currentDate);
        nextDate.setDate(currentDate.getDate() + 1);
        break;
      case 'weekly':
        nextDate = new Date(currentDate);
        nextDate.setDate(currentDate.getDate() + 7);
        break;
      case 'monthly':
        nextDate = new Date(currentDate);
        nextDate.setMonth(currentDate.getMonth() + 1);
        break;
      default:
        return null;
    }

    return nextDate;
  }

  public cyclePriority(taskId: string): Task | null {
    const tasks = this.getTasks();
    const taskIndex = tasks.findIndex(t => t.id === taskId);
    if (taskIndex === -1) return null;

    const priorityOrder: TaskPriority[] = ['low', 'medium', 'high'];
    const currentPriority = tasks[taskIndex].priority;
    const currentIndex = priorityOrder.indexOf(currentPriority);
    const nextPriority = priorityOrder[(currentIndex + 1) % priorityOrder.length];

    tasks[taskIndex].priority = nextPriority;
    this.saveTasks(tasks);
    return tasks[taskIndex];
  }

  public setReminder(taskId: string, reminderDate: Date | undefined): Task | null {
    return this.updateTask(taskId, {
      reminder: reminderDate,
      notificationTime: reminderDate,
      notificationEnabled: !!reminderDate,
    });
  }
}

export const taskStorage = new TaskStorage(); 